export default {
  name: 'VkUpload',
  data: () => ({
    dragged: false
  }),
  methods: {
    dropped: e => {
      if (e.dataTransfer && e.dataTransfer.files) {
        e.stopPropagation()
        e.preventDefault()
        this.dragged = false
        this.$emit('dropped', e.dataTransfer.files)
      }
    }
  }
}



