import core from './core'

export default {
  name: 'VkTabs',
  extends: core,
  props: {
    // tabs width using UIkit grid
    width: {
      type: String,
      default: ''
    }
  },
  beforeMount () {
    this.updateTabs()
  },
  beforeUpdate () {
    this.updateTabs()
  },
  methods: {
    updateTabs () {
      this.$slots.default.filter(c => c.tag).forEach((tab, index) => {
        if (this.width) {
          tab.data.staticClass = `uk-width-${this.width}`
        }
      })
    }
  }
}



